.regForm {
  margin: 8% auto;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid #e0e0e0;
  border-radius: 10px;
  box-shadow: 2px 2px 2px rgb(0, 0, 0, 0.3);
}
.regForm p {
  font-size: 24px;
  margin-block: 12px;
}
.regForm hr {
  width: 100%;
  border: none;
  height: 2px;
  background-color: #e0e0e0;
  margin-bottom: 4px;
}
.regForm label {
  display: block;
  font-size: 14px;
  margin-top: 14px;
}
.regForm input {
  display: block;
  width: 300px;
  min-height: 40px;
  border: 1px solid #e0e0e0;
  border-radius: 5px;
  padding: 3px 15px;
  font-size: 16px;
}
.regForm input:active,
.regForm input:focus,
.regForm input:hover {
  outline: none;
  outline-offset: none;
}
.regForm button {
  height: 45px;
  width: 330px;
  margin-top: 22px;
  margin-bottom: 22px;
  font-size: 18px;
  letter-spacing: 1.2px;
  border: none;
  border-radius: 5px;
  color: #fff;
  background-color: hsl(0, 99%, 61%);
  cursor: pointer;
  transition-duration: 0.2s;
}
.regForm button:hover {
  background-color: hsl(0, 99%, 55%);
}
.error {
  font-size: 12px;
  max-width: 300px;
  margin-top: 0;
  color: hsl(0, 99%, 61%);
}
